<script>
  import { mapState, mapGetters } from "vuex";
  import orginatordialog from "../dailog/originator_dailog.vue";
  import processSelection from "../dailog/processSelection.vue";
  import util from "../../js/util";
  import moment from "moment";
  import loading from "../../plugins/loading/loading";
  import page from "../../plugins/page/index";
  import Choosen from '../biz/compositeChoosen.vue'
  export default {
    name: "burseProcess",
    components: {
      page: page,
      Choosen:Choosen
    },
    mounted() {
      this.getList();
      var that = this;
      //审批 系统页面 操作后 返回信息
      window.onmessage = function(e) {
        var data = e.data;
        // 点击确认按钮的key
        if (data.key === "refreshNewSystem") {
          that.getList();
        }
      };
    },
    data() {
      var validateNUll = (rule, value, callback) => {
        if (value.trim() == '') {
          callback(new Error('请输审批意见！意见不能为空！'));

        } else {
          callback();
        }
      }
      return {
        // 日期禁用
        pickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.model.date[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        pickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.model.date[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },
        rules: {
          checkComment: [
            { validator: validateNUll, trigger: 'blur' }
          ]
        },
        model: {
          selectForm: {
            searchWord: "", //搜索
            startDate: "", //开始时间
            finishDate: "", //结束时间
            amountOfMoneyB: "", //金额低
            amountOfMoneyE: "", //金额高
            processFullName: "", //流程类别
            companyCode:'',//公司 （ID）
            departmentID: "", //部门（ID）
            projectId:'',  //项目（ID）
            originator: "", //发起人
            orderBy: "riskLevel desc,startDate desc" //排序
          },
          date: [],
          userName: [] //发起人
        },
        isSeach: false, //控制发起人搜索查询
        oneCheck: false,//公共控件是否单选
        queryName: "",//选择人控件搜索条件值
        // 流程类别名称
        processFormName: "",
        childDataUrl: {
          url: "",
          selectedPerson: [],
          chooseProject: {}
        }, //传给子组件的值
        loading: false,
        showdialog: false,
        //component 弹窗 子组件 所有参数
        componentParam: {
          componentType: "", //弹窗类型
          //子组件api接口路径(一接口名为属性key)
          childDataUrl: {
            type: Object,
            required: true
          },
          //需要 回填到组件上的 数据集合
          backfillData: {
            type: Array,
            required: true
          },
          //子组件是否单选(true单选，false不是单选)
          oneCheck: {
            type: Boolean,
            required: true
          },
          //控制弹窗头部标题显示隐藏(默认显示)
          closeShow: {
            type: Boolean,
            required: true
          }
        },
        //动态变更弹出框组件
        comp: {},
        orgStates: [], //发起人  搜索数据
        showList: false, //列表显示状态
        // 行数据-传给子组件
        rowData: {},
        pageIndex: 1, // 当前页码
        total: 0,     // 总条目数
        pageSize: 20, // 每页显示 条目数
        pageSizesList: [20, 50, 100],
        multipleSelection: [], // 表格选中行数据
        endsDialog: false, //取消流程
        approvaldialog: false, //审批意见弹窗控制
        //审批意见 from
        carefulform: {
          agree: "Approved", //状态
          checkComment: "", //审批意见
          iiSServerUrl:"",//空
          snList:"",//sn 用','号分割
          userName: localStorage.getItem('username'), //办理人
          handtime: moment(new Date()).format('YYYY-MM-DD') //办理时间
        },
        tableData: [], //代办表格数据
        // 加载动画
        imgLoading: false,
        dataTitle: "",
        originatorPageIndex: 0,
        originatorPageSize: 10,
        originatorTotal: 0,
        originatorLoading: false,
        typeTab:'User',
        Visible:false,
        //查询汇总数据
        serachSlectData:{
          tagsOwnsUser:[],
          tagsOwnProcess:[],//tags带来的数据
          tagsOwnCategory:[],//流程类别
          tagsOrgSelect:[],
        },
        portalmMtilselectCheck:'true',//单选多选配置
        CriteriaModel:{
          ProcessNum:'',//单号
          ProcessCode :[], //流程名称
          ProcessCategory:[],//流程类别
          DepartmentId :'',//部门
          FormSubject :'', //主题
          Originator :'', //申请人
          SubmitStartDate :'', //开始时间
          SubmitEndDate :'',  //结束时间
          approvers:'', //当前审批人
          SortField :'', //排序字段
          SortDirection :'', //排序方式
//          status:'', //流程状态
//          total:0,//总数量
          pageSizesList: [10,20],
          pageIndex:1, //当前页码
          pageSize:10 //每页数量
        },//获取我的代办初始参数定义
        ProcessType:''
      };
    },
    methods: {
      /**
       * 埋点事件
       */
      burialPointEvent(event){
        util.burialPoint(event);
      },
      // 加载更多
      loadMore(value) {
        this.originatorPageIndex ++;
        this.getUsers(value);
      },
      //关闭 批量审批 弹窗
      closeApproval(){
        //重置 验证
        this.$refs.carefulform.clearValidate();
      },
      //提交之前验证
      ascertain(carefulform){
        this.$refs[carefulform].validate((valid) => {
          if (valid) {
            this.submitApproval()
          } else {
            return false;
          }
        });
      },
      // 批量审批
      submitApproval(){
        this.approvaldialog = false;
        let list = [];
        let AuditModel = {};
        for(let item of this.multipleSelection){
          list.push(
            AuditModel={
              SharedUser :item.SharedUser,
              WorkItemId:item.Id,
              Comment:this.carefulform.checkComment
            }
          )
        }

        this.$axios.post("/Portal/WorkFlow/MutliApprove",list).then(result => {
          if(result.data && result.data.code===200){
            this.$store.dispatch('GETTODO');
            this.$message.success('审批成功!');
          }
          this.getList();
        }).catch(err => {
          this.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        });
      },
      // 结束流程
      endProcess() {
        this.endsDialog = false;
        let snList = this.getSN() //获取sn
        let sn= snList.join(',')  //sn 用','号分割
        let procInstId = this.multipleSelection[0].procInstID //获取 procInstID

        this.$axios.post("/api/stopProcessInstances", {procInstId,sn}).then(res => {
          let data = eval("(" + res.data.result + ")");
          if (data.Status === "4") {
            //如果结束流程成功，再从新刷新列表
            this.$message.success(`${data.Message}`);
            this.getList();
          } else {
            this.$message.warning(`${data.Message}`);
            this.$refs.table.clearSelection(); // 清除选中状态
          }
        })
          .catch(err => {
            this.$message.error(`结束流程失败！${err}`);
          });
      },
      //获取sn
      getSN(){
        let snList = []
        for(let item of this.multipleSelection){
          let sn = item.viewUrl.substr(item.viewUrl.indexOf("SN=") + 3,item.viewUrl.length)
          sn = sn.substr(0, sn.indexOf("&"));
          snList.push(sn)
        }
        return snList
      },
      //办理类型 number 转换 文字
      formatter(row) {
        switch (row.dodoType) {
          case "1":
            row.statusName = "审批";
            break;
          case "2":
            row.statusName = "授权";
            break;
          case "3":
            row.statusName = "意见反馈";
            break;
          case "4":
            row.statusName = "被退回";
            break;
          default:
            row.statusName = "";
            break;
        }
        return row;
      },
      //表格排序
      sortChange(column) {
        this.CriteriaModel.SortDirection  = `${column.order.replace('ending','')}`;
        this.CriteriaModel.SortField   = column.prop;
        this.pageIndex = 1;
        this.getList();
      },
      //设置表格列头
      renderIcon(create, { column }) {
        return create("i", { class: "iconfont icon-process" });
      },
      /**
       * 发起人搜索
       *
       */
      getUsers(userName) {
        this.originatorLoading = true;
        let that = this;
        that.$axios.post(`/api/getUser?pageIndex=${this.originatorPageIndex}&pageSize=${this.originatorPageSize}`, { name: userName, deptId: "", isWork: "1" }).then(result => {
          this.originatorTotal = result.data.pageControllerInfo.totalRecordCount;
          for (let i = 0; i < result.data.result.length; i++) {
            that.orgStates.push({
              value: result.data.result[i].displayName,
              userId: result.data.result[i].adAccount,
              companyName: result.data.result[i].companyName,
              orgFullPath:result.data.result[i].orgNameFullPath,
              vankeId: result.data.result[i].vankeId
            });
          }
          this.loading = false;
          this.originatorLoading = false;
        }).catch(err => {
          this.originatorLoading = false;
          that.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        });
      },
      /**
       * 发起人模糊搜索
       */
      querySearchUser(userName) {
        this.orgStates = [];
        var that = this;
        that.queryName = userName;
        this.loading = true;
        if (this.isSeach) {
          return;
        }
        this.isSeach = true;
        setTimeout(() => {
          that.isSeach = false;
          that.validationInput(that.queryName);
        }, 2000);
      },
      // 发起人选择改变
      personChange(data) {
        util.burialPoint('我的待办-输入条件-发起人输入框');
        this.$set(this.$refs.userId, "query", ""); //选中后清空输入的文本
        for (var i = 0; i < data.length; i++) {
          var bool = false;
          for (var j = 0; j < this.childDataUrl.selectedPerson.length; j++) {
            // 先判断data的userId是否存在
            if (
              !data[i].userId ||
              this.childDataUrl.selectedPerson[j].userId === data[i].userId
            ) {
              bool = true;
            }
          }
          if (!bool) {
            this.childDataUrl.selectedPerson.push(data[i]);
          }
        }
      },
      //下拉显示隐藏
      visibleChange() {
        this.orgStates = [];
      },
      //删除用户
      removeUser(data) {
        if (data.userId) {
          for (var it = 0; it < this.childDataUrl.selectedPerson.length; it++) {
            if (data.userId == this.childDataUrl.selectedPerson[it].userId) {
              break;
            }
          }
          this.childDataUrl.selectedPerson.splice(it, 1);
        } else {
          for (var it = 0; it < this.childDataUrl.selectedPerson.length; it++) {
            if (data == this.childDataUrl.selectedPerson[it].value) {
              break;
            }
          }
          this.childDataUrl.selectedPerson.splice(it, 1);
        }
      },
      //验证延时后的值是否跟当前文本框值相同
      validationInput(userName) {
        this.orgStates = [];
        if (userName == "" || userName.length == 0) {
          this.loading = false;
          return null;
        }
        if (userName == this.$refs.userId.query) {
          this.getUsers(userName);
        } else {
          this.orgStates = [];
        }
      },
      //-------------------------------      弹窗

      /**
       * 打开弹出框组件
       */
      openDailog(type) {
        this.componentParam.closeShow = false;
        // 弹出框类型
        if (type == "originator_dailog") {
          util.burialPoint('我的待办-输入条件-发起人输入框');
          //发起人
          this.oneCheck = false;
          this.childDataUrl.getTree = "getTree";
          this.childDataUrl.companytree = "companytree/page";
        } else if (type == "processSelection") {
          util.burialPoint('我的待办-输入条件-流程类别');
          //新流程选择
          // 弹出框类型
          this.componentParam.componentType = type;
          //是否单选
          this.componentParam.oneCheck = true;
        }
        this.comp.component = type;
        this.showdialog = true;
        util.showDailog_mask(); //解决ie浏览器问题显示遮罩
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask(); //解决ie浏览器问题 隐藏遮罩
      },
      /**
       * 点击公众弹窗确定
       * 弹窗组件带出数据赋值给主组件
       * @param {Object} data  回传的数据
       */
      showMsgfromchild(data) {
        if (data.type && data.type == "originator") {
          // 选择发起人
          this.childDataUrl.selectedPerson = [];
          this.model.userName = [];
          //选人控件 赋值时 筛选重复数据
          for (let origina of data.peoArr) {
            var userId = origina.userId;
            var vankeId = origina.vankeId;
            var bool = false;
            for (var j = 0; j < this.childDataUrl.selectedPerson.length; j++) {
              if (this.childDataUrl.selectedPerson[j].userId === userId) {
                bool = true;
              }
            }
            if (!bool) {
              this.childDataUrl.selectedPerson.push({
                userId: userId,
                value: origina.name,
                vankeId: vankeId
              });
              this.model.userName.push(origina.name);
            }
          }
        } else if (data.type && data.type == "processSelection") {
          util.burialPoint('我的待办-输入条件-流程类别');
          //选择流程类别
          this.componentParam.backfillData = data.list;
          let processId = [];
          let processName = [];
          for (let node of data.list) {
            processName.push(node.name);
            processId.push(node.processId + "");
          }
          this.model.selectForm.processFullName = processId.join(",");
          this.processFormName = processName.join("；");
        }
        this.hideComponent();
      },
      //-------------------------------      弹窗
      //刷新查询条件
      refresh() {
        util.burialPoint('我的待办-输入条件-重置按钮');
        this.CriteriaModel.ProcessNum='';
        this.serachSlectData.tagsOwnProcess = [];
        this.serachSlectData.tagsOwnCategory = [];
        this.CriteriaModel.ProcessCategory = [];
        this.CriteriaModel.Originator = '';
        this.CriteriaModel.DepartmentId = '';
        this.CriteriaModel.FormSubject = '';
        this.serachSlectData.tagsOrgSelect =[];
        this.serachSlectData.tagsOwnsUser = [];
        this.model.date = []; //发起时段
      },
      //金额输入中验证
      inputValidate(position, value) {
        let that = this;
        let regPos = /^([0-9]*)([.]?|(\.\d{1,2})?)$/; //验证金额正则
        if (position == "front") {
          if (!regPos.test(value)) {
            setTimeout(function() {
              that.model.selectForm.amountOfMoneyB = "";
            }, 100);
          }
        } else if (position == "back") {
          if (!regPos.test(value)) {
            setTimeout(function() {
              that.model.selectForm.amountOfMoneyE = "";
            }, 100);
          }
        }
      },
      //金额输完验证
      checkValidate(position, value) {
        value = parseFloat(value);
        let that = this;
        if (position == "front") {
          if (this.model.selectForm.amountOfMoneyE != "" && value > parseFloat(this.model.selectForm.amountOfMoneyE)) {
            that.model.selectForm.amountOfMoneyB = "";
          }
        } else if (position == "back") {
          if (this.model.selectForm.amountOfMoneyB != "" && value < parseFloat(this.model.selectForm.amountOfMoneyB)) {
            that.model.selectForm.amountOfMoneyE = "";
          }
        }
      },
      //搜索
      search() {
        util.burialPoint('我的待办-输入条件-搜索按钮');
        this.pageIndex = 1;
        this.getList();
      },
      //获取列表
      getList() {
        var that = this;
        that.model.date == null ? (that.model.date = []) : "";
        that.CriteriaModel.SubmitStartDate = that.model.date[0]? `${that.model.date[0]} 00:00:00`: "";
        that.CriteriaModel.SubmitEndDate = that.model.date[1]? `${that.model.date[1]} 23:59:59`: "";
        that.CriteriaModel.ProcessNum = that.CriteriaModel.ProcessNum.replace(/\s+/g,"");
        that.CriteriaModel.FormSubject = that.CriteriaModel.FormSubject.replace(/\s+/g,"");
        //申请人
        if(that.serachSlectData.tagsOwnsUser[0]){
          that.CriteriaModel.Originator = that.serachSlectData.tagsOwnsUser[0].UserAccount;
        }else {
          that.CriteriaModel.Originator = ''
        }
        //部门
        if( that.serachSlectData.tagsOrgSelect[0]){
          that.CriteriaModel.DepartmentId = that.serachSlectData.tagsOrgSelect[0].UserAccount;
        }else {
          this.CriteriaModel.DepartmentId = '';
        }
//        //流程
        if( that.serachSlectData.tagsOwnProcess){
          that.CriteriaModel.ProcessCode = [];
          for(let item of this.serachSlectData.tagsOwnProcess){
            that.CriteriaModel.ProcessCode.push(item.processCode);
          }
        }
        //流程类别
        if(that.serachSlectData.tagsOwnCategory){
          that.CriteriaModel.ProcessCategory = [];
          for(let v of that.serachSlectData.tagsOwnCategory){
            that.CriteriaModel.ProcessCategory.push(v.id)
          }
        }
        // 控制日期选择区间
        if( (that.model.selectForm.SubmitStartDate < '1753-01-01' && that.model.selectForm.SubmitStartDate !== '') || (that.model.selectForm.SubmitEndDate < '1753-01-01' && that.model.selectForm.SubmitEndDate !== '') ) {
          this.$message.warning('请选择1753-01-01 ~ 9999-12-31之间的日期');
          // this.model.date = [];
          return;
        }
        that.imgLoading = true;
        // 分页信息
        that.$axios({
          url:'/Portal/WorkFlow/GetWorklistExpenseReimbursement',
          data:that.CriteriaModel,
          method:'post'
        }).then(result => {
          if(result.data.code === 200) {
            // 已办数据集合
            that.tableData = result.data.data.item;
            // 数据总数
            that.CriteriaModel.total = result.data.data.total;
          }
          else {
            this.$message.error("请求失败！");
          }
         this.imgLoading = false;
        }).catch(err => {
          this.imgLoading = false;
          this.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        });
      },
      //点击 每页显示条目数
      handleSizeChange(val) {
        util.burialPoint('我的待办-列表-每页显示条数按钮');
        var pageSize =  val;
        this.CriteriaModel.pageIndex = 1;
        this.CriteriaModel.pageSize = parseInt(pageSize);
        localStorage.todoPageSize = val;
        this.getList();
      },
      //点击页码
      handleCurrentChange(val) {
        util.burialPoint('我的待办-列表-页码按钮');
        this.CriteriaModel.pageIndex = val;
        this.getList();
      },
      //设置表头标题class
      tableHeaderClassName({ row, rowIndex }) {
        return "table-head-th";
      },
      // 改变表格行颜色    红色：#ff0000;橘色：#ffa500;
      tableRowClassName({row, rowIndex}){
        if (row.riskLevel == 2) {
          return 'orange-row';
        } else if (row.riskLevel == 3) {
          return 'red-row';
        }
        return '';
      },
      //全选
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },

      //父组件打开弹窗
      choosenDialogType(val,type){
        var that = this;
        that.typeTab = val;
        that.ProcessType = type;
        that.$refs.dialogChoosen.openChoosen(that.typeTab,type);
      },
      //打开弹窗
      OpenDialog(val){
        var that = this;
        that.Visible = val
      },
      //关闭弹窗
      CloseDialog(val){
        var that = this;
        that.Visible = val;
      },
      //接收子组件确定按钮带过的数据
      sendTag(val,type){
        let _this = this;
        if(val.length > 0){
          for(let item of val){
            if(item.type==='User'){
              _this.serachSlectData.tagsOwnsUser = [];
              if(_this.serachSlectData.tagsOwnsUser.length>0){
                for (let i of _this.serachSlectData.tagsOwnsUser){
                  if(i.SysId===item.SysId){
                    return
                  }else {
                    _this.serachSlectData.tagsOwnsUser.push(item);
                  }
                }
              }
              else {
                _this.serachSlectData.tagsOwnsUser.push(item);
              }
            }
            else if(item.type==='Process'){
              if(_this.serachSlectData.tagsOwnProcess.length>0){
                for (let i of _this.serachSlectData.tagsOwnProcess){
                  if(i.id!==item.id){
                    _this.serachSlectData.tagsOwnProcess.push(item);
                  }
                }
              }
              else {
                _this.serachSlectData.tagsOwnProcess.push(item);
              }
            }
            else if(item.type==='Category'){
              if(_this.serachSlectData.tagsOwnCategory.length>0){
                for (let i of _this.serachSlectData.tagsOwnCategory){
                  if(i.id!==item.id){
                    _this.serachSlectData.tagsOwnCategory.push(item);
                  }
                }
              }
              else {
                _this.serachSlectData.tagsOwnCategory.push(item);
              }
            }
            else {
              _this.serachSlectData.tagsOrgSelect = [];
              if(_this.serachSlectData.tagsOrgSelect.length>0){
                for (let i of _this.serachSlectData.tagsOrgSelect){
                  if(i.SysId!==item.SysId){
                    _this.serachSlectData.tagsOrgSelect.push(item);
                  }
                }
              }
              else {
                _this.serachSlectData.tagsOrgSelect.push(item);
              }
            }
          }
        }
        else {
          switch (type){
            case 'Process':{
              _this.serachSlectData.tagsOwnProcess = [];
              break
            }
            case 'Category':{
              _this.serachSlectData.tagsOwnCategory = [];
              break;
            }
            case 'User':{
              _this.serachSlectData.tagsOwnsUser = [];
              break;
            }
            case 'Org':{
              _this.serachSlectData.tagsOrgSelect = [];
              break
            }
          }
        }
      },
      //删除页面回显中的tag
      closeProcessTag(item){
        switch (item.type){
          case 'Process':{
            let index =  this.serachSlectData.tagsOwnProcess.indexOf(item);
            this.serachSlectData.tagsOwnProcess.splice(index,1);
            this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnProcess);
            break;
          }
          case 'Category':{
            let index =  this.serachSlectData.tagsOwnCategory.indexOf(item);
            this.serachSlectData.tagsOwnCategory.splice(index,1);
            this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnCategory);
            break;
          }
          case 'User':{
            let index = this.serachSlectData.tagsOwnsUser.indexOf(item);
            this.serachSlectData.tagsOwnsUser.splice(index,1);
            this.CriteriaModel.Originator = '';
            this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOwnsUser,null)
            break;
          }
          case 'Org':{
            let index = this.serachSlectData.tagsOrgSelect.indexOf(item);
            this.serachSlectData.tagsOrgSelect.splice(index,1);
            this.CriteriaModel.DepartmentId = '';
            this.$refs.dialogChoosen.portalehco(this.serachSlectData.tagsOrgSelect,null)
            break;
          }
        }
      },
      /**
       * 点击主题跳转到旧界面
       */
      jumpToView(row) {
        window.open(row.FlowViewUrl,'_blank');
        var mychar = document.getElementById('dailog_mask');
        mychar.style.display = 'none';
      },
      //流程单号跳转
      folioBlank(row){
        window.open(row.Url,'_blank');
      },
      //初始设置是否勾选
      checkBoxForm(row,index){
        if(row.IsBatchApproval){
          return 1;
        }else{
          return 0;
        }
      },
    },
    watch: {
      "model.userName": {
        handler: function(n, o) {
          //发起人输入框高度控制
          let ele = this.$refs.userId;
          util.inputHeight(n, ele);

          this.dataTitle = "";
          // 监听数组list 的第一项的name值变化；n 为新数据，o 为旧数据
          for (var i = 0; i < n.length; i++) {
            if (typeof n[i] == "object") {
              this.dataTitle += n[i].value + ",";
            } else if (typeof n[i] == "string") {
              this.dataTitle += n[i] + ",";
            }
          }
          this.dataTitle = this.dataTitle.substr(0, this.dataTitle.length - 1);
        },
        deep: true
      },
      Visible:{
        handler(n, o) {
          if(n){
            switch (this.typeTab){
              case 'User':{
                this.echoData = [...this.serachSlectData.tagsOwnsUser];
                break
              }
              case 'Org':{
                this.echoData = [...this.serachSlectData.tagsOrgSelect];
                break
              }
            }
            switch (this.ProcessType){
              case 'Process':{
                this.echoData = [...this.serachSlectData.tagsOwnProcess];
                break
              }
              case 'Category':{
                this.echoData = [...this.serachSlectData.tagsOwnCategory];
                break
              }
            }
          }
        }
      },
    },
    computed: {
      bodyHeight() {
        return document.documentElement.clientHeight;
      },
      ...mapGetters(["isIE", "todoCount", "isSafari", "bodyHeight"])
    }
  };
</script>

<style lang="scss" scoped>

  // 批量审批 (待办单独弹窗) begin
  #wait-opinion-dialog{
    .el-dialog{
      width: 35%;
      .el-dialog__body{
        .el-form-item {
          margin-bottom: 10px;
          text-align: left;
        }
      }
      .el-button:first-child{
        background-color: #409EFF;
        color:#fff;
      }
    }
  }
  // 批量审批 (待办单独弹窗) end

  //弹窗样式 begin
  .el-dialog__wrapper {
    position: fixed!important;
    .el-dialog {
      .approvaldialog-title {
        background-color: #f0f3f8;
      }
      .el-dialog__body {
        padding: 5px 20px !important;
        color: #606266;
        font-size: 14px;
      }
    }
  }
  //弹窗样式 end
</style>




